import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Form, Input, Modal} from "semantic-ui-react";
import eventEmitter from "../../commons/eventEmitter";
import actions from "../../actions";

class EditClusterPgDatabaseForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            editType: 'add',//add || edit
            name: '',
            owner: '',
            schemas: [],
            extensions: [],
            parameters: {},
            comment: '',
            pgbouncer: true,
            allowconn: true,
            revokeconn: false,
            clusterName: null,
            parameterKey: '',
            parameterValue: '',
            extensionKey: '',
            extensionValue: '',
        }
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showEditClusterPgDatabaseForm', (data) => {
            this.setState({
                open: true,
                ...data
            })
        })
    }

    setOpen(flag) {
        this.setState({
            open: flag
        })
    }

    _inputHandle(name, value) {
        this.setState({
            [name]: value
        })
    }

    _addNewClusterPgDatabase() {
        const {
            addNewClusterPgDatabase
        } = this.props;
        const {
            name,
            owner,
            schemas,
            extensions,
            parameters,
            comment,
            pgbouncer,
            allowconn,
            revokeconn,
            clusterName
        } = this.state;
        let formData = {
            name,
            owner,
            schemas,
            pgbouncer,
            allowconn,
            revokeconn,
            parameters
        }
        if(comment.length > 0){
            formData.comment = comment
        }
        if(extensions.length > 0){
            formData.extensions  = extensions;
        }

        // if(pgbouncer != null){
        //     formData.pgbouncer = pgbouncer
        // }
        // if(allowconn != null){
        //     formData.allowconn = allowconn
        // }
        // if(revokeconn != null){
        //     formData.revokeconn = revokeconn
        // }

        if (clusterName) {
            addNewClusterPgDatabase({
                formData:formData,
                clusterName
            }, (backData) => {
                if(backData.success){
                    this.setState({
                        open: false
                    })
                }
            })
        }
    }


    _addParameter() {
        const {
            parameterKey,
            parameterValue
        } = this.state;
        if (parameterKey.length > 0 && parameterValue.length > 0) {
            this.setState({
                parameters: {
                    ...this.state.parameters,
                    [parameterKey]: parameterValue
                },
                parameterKey: '',
                parameterValue: ''
            })
        }
    }

    _deleteParameter(parameterKey) {
        delete this.state.parameters[parameterKey];
        this.setState({
            parameters: {
                ...this.state.parameters
            }
        })
    }

    _addExtension() {
        const {
            extensionKey,
            extensionValue
        } = this.state;
        if (extensionKey.length > 0) {
            let newExtension = {
                name: extensionKey
            }
            if (extensionValue.length > 0) {
                newExtension.schema = extensionValue
            }
            this.setState({
                extensions: [
                    ...this.state.extensions,
                    newExtension
                ],
                extensionKey: '',
                extensionValue: ''
            })
        }
    }

    _deleteExtension(extension) {
        let newE = this.state.extensions.map((eItem, eIndex) => {
            return JSON.stringify(eItem) != JSON.stringify(extension)
        })
        this.setState({
            extensions: newE
        })
    }


    render() {

        const {
            open,
            name,
            owner,
            parameters,
            schemas,
            extensions,
            comment,
            pgbouncer,
            allowconn,
            revokeconn,
            editType,
            parameterKey,
            parameterValue,
            extensionKey,
            extensionValue,
        } = this.state;

        let _renderParameters = Object.keys(parameters).map((pItem, pIndex) => {
            return (
                <Form.Group widths='equal' className='m'>
                    <Form.Input value={pItem} placeholder='key'
                                disabled
                                style={{color: '#333'}}
                    />
                    <Form.Input value={parameters[pItem]} placeholder='value'
                                disabled
                                style={{color: '#333'}}
                    />
                    <Button type='submit' icon='minus'
                            onClick={this._deleteParameter.bind(this, pItem)}
                    ></Button>
                </Form.Group>
            )
        })

        let _renderExtensions = extensions.map((eItem, eIndex) => {
            return (
                <Form.Group widths='equal' className='m'>
                    <Input value={eItem.name} placeholder='name'
                           disabled
                           label='name'
                           style={{color: '#333'}}
                           className='field'
                    />
                    {eItem.schema ? (
                        <Input value={eItem.schema} placeholder='schema'
                               disabled
                               label='schema'
                               style={{color: '#333'}}
                               className='field'
                        />
                    ) : null}

                    <Button type='submit' icon='minus'
                            onClick={this._deleteExtension.bind(this, eItem)}
                    ></Button>
                </Form.Group>
            )
        })


        return (
            <Modal
                onClose={this.setOpen.bind(this, false)}
                open={open}
                size='small'
            >
                <Modal.Header>{editType === 'add' ? 'Add Cluster Host' : 'Edit Cluster Host'}</Modal.Header>
                <Modal.Content>
                    <Form>
                        <Form.Group widths='equal' className='border-bottom pb-5px'>
                            <Form.Field>
                                <label>name</label>
                                <Input
                                    placeholder=''
                                    name='name'
                                    value={name}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>
                            <Form.Field>
                                <label>owner</label>
                                <Input
                                    placeholder=''
                                    name='owner'
                                    value={owner}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>
                        </Form.Group>

                        <Form.Field>
                            <label>comment</label>
                            <Input
                                placeholder=''
                                name='comment'
                                value={comment}
                                onChange={(e, {name, value}) => {
                                    this._inputHandle(name, value)
                                }}
                            />
                        </Form.Field>

                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>pgbouncer</label>
                                <Form.Radio
                                    label='true'
                                    name='pgbouncer'
                                    checked={pgbouncer === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='pgbouncer'
                                    checked={pgbouncer === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                            <Form.Group inline className='flex-1'>
                                <label>allowconn</label>
                                <Form.Radio
                                    label='true'
                                    name='allowconn'
                                    checked={allowconn === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='allowconn'
                                    checked={allowconn === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                        </Form.Group>
                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>revokeconn</label>
                                <Form.Radio
                                    label='true'
                                    name='revokeconn'
                                    checked={revokeconn === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='revokeconn'
                                    checked={revokeconn === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>
                        </Form.Group>


                        <Form.Field>
                            <label>schemas (Separated by commas)</label>
                            <Input
                                placeholder='schema,schema'
                                name='schemas'
                                value={schemas.join(',')}
                                onChange={(e, {name, value}) => {
                                    this._inputHandle(name, value.split(','))
                                }}
                            />
                        </Form.Field>

                        <Form.Field>
                            <label>parameters</label>
                            <Form.Group widths='equal'>
                                <Form.Input name='parameterKey' value={parameterKey} placeholder='key'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Form.Input name='parameterValue' value={parameterValue} placeholder='value'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Button type='submit' icon='plus'
                                        onClick={this._addParameter.bind(this)}
                                ></Button>
                            </Form.Group>
                            <div>
                                {_renderParameters}
                            </div>
                        </Form.Field>

                        <Form.Field>
                            <label>extensions</label>
                            <Form.Group widths='equal'>
                                <Form.Input name='extensionKey' value={extensionKey} placeholder='name'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Form.Input name='extensionValue' value={extensionValue} placeholder='schema'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Button type='submit' icon='plus'
                                        onClick={this._addExtension.bind(this)}
                                ></Button>
                            </Form.Group>
                            <div>
                                {_renderExtensions}
                            </div>
                        </Form.Field>


                    </Form>
                </Modal.Content>
                <Modal.Actions>
                    <Button basic
                            onClick={this.setOpen.bind(this, false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        primary
                        onClick={this._addNewClusterPgDatabase.bind(this)}
                    >Save</Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({
    configData: state.configData
});
const mapDispatchToProps = dispatch => ({
    addNewClusterPgDatabase: (formParams, callBack) => dispatch(actions.addNewClusterPgDatabase(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditClusterPgDatabaseForm));
