import React from 'react'
import {Confirm} from 'semantic-ui-react'
import eventEmitter from "../commons/eventEmitter";

export default class ConfirmBox extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            content:'',
            confirmButton:'OK',
            cancelButton:'Cancel'
        }
        this.parentCallback = null;
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showConfirmBox',(data)=>{
            this.setState({
                ...data,
                open:true,
            });
            this.parentCallback = data.callback
        })
    }


    handleConfirm(){
        this.setState({
            content: 'Are you sure?',
            confirmButton:'OK',
            cancelButton:'Cancel',
            open: false
        },()=>{
            if(typeof this.parentCallback === 'function'){
                this.parentCallback(true)
            }
        })
    }
    handleCancel(){
        this.setState({
            content: 'Are you sure?',
            confirmButton:'OK',
            cancelButton:'Cancel',
            open: false
        },()=>{
            if(typeof this.parentCallback === 'function'){
                this.parentCallback(false)
            }
        })
    }

    render() {
        const {
            open,
            content,
            confirmButton,
            cancelButton
        } = this.state;

        return (
            <Confirm
                size='mini'
                open={open}
                content={content}
                confirmButton={confirmButton}
                cancelButton={cancelButton}
                onCancel={this.handleCancel.bind(this)}
                onConfirm={this.handleConfirm.bind(this)}
            />
        );
    }
}

