import React from 'react';
import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/addon/display/autorefresh';
import 'codemirror/addon/comment/comment';
import 'codemirror/addon/edit/matchbrackets';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import './codeEditor.css';
import {Button, Modal} from "semantic-ui-react";
import eventEmitter from "../commons/eventEmitter";
import yaml from "js-yaml";

export default class CodeEditor extends React.Component {
    constructor(props) {
        super(props);
        this.codeM = null;
        this.state = {
            open:false,
            codeData:'',
            title:'code editor'
        }
    }


    componentDidMount() {
        this.emitter = eventEmitter.addListener('showCodeEditor',(data)=>{
            this.setState({
                open:true,
                codeData:data.codeData,
                title:data.title || 'Code editor',
            },()=>{
                this.parentCallback = data.callback;
            })
        })
    }

    closeModal(){
        this.setState({
            open:false
        })
    }

    saveCode(){
        let code = this.codeM.editor.getValue();

        let JSONYaml = null;
        try {
            JSONYaml = yaml.load(code);
        } catch (e) {
            eventEmitter.emit('showToasts',{
                type:'error',
                msg:e.toString()
            })
        }

        if(JSONYaml){
            this.setState({
                open:false
            },()=>{
                this.parentCallback(code);
            })
        }


    }

    render() {
        const {
            open,
            title,
            codeData
        } = this.state;
        let that = this;
        return (
            <Modal
                closeIcon
                open={open}
                centered={false}
                className={'modal-codeEditor'}
                onClose={this.closeModal.bind(this)}
            >
                <Modal.Header>{title}</Modal.Header>
                <Modal.Content scrolling>
            <div className="codeEditor">
                <CodeMirror
                    ref={_ref=>{
                        if(_ref?.editor){
                            that.codeM = _ref;
                        }
                    }}
                    value={codeData}
                    options={{
                        theme: 'monokai',
                        tabSize: 2,
                        keyMap: 'sublime',
                        mode: 'yaml',
                    }}
                />
            </div>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                            onClick={this.closeModal.bind(this)}
                    >
                        Close
                    </Button>
                    <Button primary
                            onClick={this.saveCode.bind(this)}
                    >
                        Save
                    </Button>
                </Modal.Actions>
            </Modal>

        )
    }
}
