import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../actions";
import {Button, Icon, List} from 'semantic-ui-react';
import eventEmitter from "../../commons/eventEmitter";

class ClusterPgDatabaseItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    _removeClusterPgDatabase(name) {
        const {
            removeClusterPgDatabase,
            clusterName
        } = this.props;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'Are you sure ?',
                callback: (flag) => {
                    if (flag) {
                        removeClusterPgDatabase({
                            name,
                            clusterName
                        }, backData => {

                        })
                    }
                }
            }
        )
    }

    render() {
        const {
            pgDatabase,
        } = this.props;
        return (
            <List.Item className='mb-5px'>
                <Button size='mini' icon='trash' basic className='right floated no-shadow'
                        onClick={this._removeClusterPgDatabase.bind(this, pgDatabase.name)}
                />
                <Icon
                    name='database'
                    className='left floated'
                    fitted
                    size='large'
                />
                <List.Content>
                    <List.Header as='a'>{pgDatabase.name}</List.Header>
                    <List.Description className='mt-5px'>
                        {pgDatabase.comment}
                    </List.Description>
                </List.Content>
            </List.Item>
        )
    }
}

const mapStateToProps = (state, props) => ({

});
const mapDispatchToProps = dispatch => ({
    removeClusterPgDatabase: (formParams, callBack) => dispatch(actions.removeClusterPgDatabase(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ClusterPgDatabaseItem));
