import React from 'react'
import {connect} from 'react-redux';
import {withRouter} from "react-router-dom";
import {Icon, Menu, Sticky} from 'semantic-ui-react'
import './header.css';

class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeItem: ''
        }
    }

    _jumpHome(){
        const {
            history,
        } = this.props;
        history.push('/');
    }

    handleItemClick(e,data){
        const {
            history,
        } = this.props;
        this.setState({
            activeItem:data.name
        })
        history.push('/'+data.name);
    }



    render() {
        const {
            location
        } = this.props;
        const {
            activeItem
        } = this.state;


        //let orgSelect = this._renderOrgSelecet();
        return (
            <Sticky>
                <div className='top-header'>
                    <a onClick={this._jumpHome.bind(this)} className="sidemenu__logo">
                        <div className="icon-menu">
                            <img src="/img/pigsty/logo-white.svg" alt="pigsty" />
                        </div>
                        <img className="menu-img" src="/img/pigsty/menulogo.svg" alt="pigsty" />
                    </a>

                    <Menu secondary className='top-header-menu'>
                        <Menu.Item
                            name=''
                            active={location.pathname === '/'}
                            onClick={this.handleItemClick.bind(this)}
                        >
                            <Icon name='sitemap' />
                            Cluster
                        </Menu.Item>
                        <Menu.Item
                            name='jobs'
                            active={location.pathname === '/jobs'}
                            onClick={this.handleItemClick.bind(this)}
                        >
                            <Icon name='terminal' />
                            Jobs
                        </Menu.Item>
                        <Menu.Item
                            name='config'
                            active={location.pathname === '/config'}
                            onClick={this.handleItemClick.bind(this)}
                        >
                            <Icon name='code' />
                            Yaml
                        </Menu.Item>
                        <Menu.Item
                            name='playbook'
                            active={location.pathname === '/playbook'}
                            onClick={this.handleItemClick.bind(this)}
                        >
                            <Icon name='paper plane outline' />
                            Playbook
                        </Menu.Item>
                        <Menu.Menu position='right'>
                            {/*<Menu.Item>*/}
                            {/*    <Input icon='search' placeholder='Search...' />*/}
                            {/*</Menu.Item>*/}
                            {/*<Menu.Item*/}
                            {/*    name='Sign Out'*/}
                            {/*    active={activeItem === 'logout'}*/}
                            {/*    onClick={this.handleItemClick.bind(this)}*/}
                            {/*/>*/}
                        </Menu.Menu>
                    </Menu>
                </div>
            </Sticky>
        );
    }
}

const mapStateToProps = (state, props) => ({
    //account: state.account,
    //managerList:state.organization.managerList
});
const mapDispatchToProps = dispatch => ({
    //logout: (formParams, callBack) => dispatch(actions.logout(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Header));
