import React from 'react'
import {connect} from 'react-redux';
import {Button, Form, Input} from "semantic-ui-react";
import './index.css'

import eventEmitter from "../../commons/eventEmitter";
import Config from "../../config";

class SandboxProvision extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            cmd:''
        }
    }

    runScript(){
        const {
            cmd
        } = this.state;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'Are you sure run this script ?',
                confirmButton:'Run',
                callback:(flag)=>{
                    if(flag){
                        eventEmitter.emit(
                            'showCmdViewer',
                            {
                                sseUrl:Config.officialServerUrl+'/api/v1/sandbox '+cmd,
                                eventName: 'pigsty-cli'
                            }
                        )
                    }
                }
            }
        )
    }

    render() {
        const {} = this.props
        return (
            <div className='playbook-item'>
                <div className='playbook-item-intro'>
                    <h1>Sandbox Provision</h1>
                    <div className="lead">How to perform one-pass bootstrap for sandbox?</div>

                </div>


                <Form className='playbook-item-run-script'>
                    <Form.Field>
                        <label>Run Script</label>
                        <Form.Group inline>
                            <code className='code' style={{width: '12rem'}}>./sandbox.yml </code>
                            <Input fluid
                                   className='code'
                                   placeholder='--tags='
                                   onChange={(e,{name,value})=>{
                                       this.setState({
                                           cmd:value
                                       })
                                   }}
                            />
                            <Button primary type='submit'
                                    onClick={this.runScript.bind(this)}
                            >Run</Button>
                        </Form.Group>
                    </Form.Field>
                </Form>

            </div>

        );
    }
}

const mapStateToProps = (state, props) => ({});
const mapDispatchToProps = dispatch => ({});

export default connect(mapStateToProps, mapDispatchToProps)(SandboxProvision);
