import React from 'react'
import {connect} from 'react-redux';
import {Button, Form, Input} from "semantic-ui-react";
import './index.css'

import eventEmitter from "../../commons/eventEmitter";
import Config from "../../config";

class InfraProvision extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            cmd:''
        }
    }

    runScript(){
        const {
            cmd
        } = this.state;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'Are you sure run this script ?',
                confirmButton:'Run',
                callback:(flag)=>{
                    if(flag){
                        eventEmitter.emit(
                            'showCmdViewer',
                            {
                                sseUrl:Config.officialServerUrl+'/api/v1/infra '+cmd,
                                eventName: 'pigsty-cli'
                            }
                        )
                    }
                }
            }
        )
    }

    render() {
        const {} = this.props
        return (
            <div className='playbook-item'>
                <div className='playbook-item-intro'>
                    <h1>Infra Provision</h1>
                    <div className="lead">How to pull up infrasturcture on meta node</div>

                    <h2 id="overview">Overview</h2>
                    <p>Infrastructure initialization is done via <a
                        href="https://github.com/Vonng/pigsty/blob/master/infra.yml"><code>infra.yml</code></a>. The script
                        completes the installation and deployment of <strong>infrastructure</strong> on the <strong>meta
                            node</strong>.</p>
                    <p>``infra.yml<code>takes the metanode (default group name</code>meta`) as the deployment target.</p>
                    <div className="highlight"><pre><code
                        className="language-bash" data-lang="bash">. /infra.yml
</code></pre>
                    </div>
                    <p>! <a href="/img/play/infra.svg"></a></p>
                    <h2 id="caution">Caution</h2>
                    <p><strong> The initialization of the meta node must be completed before the initialization of the
                        database node can be executed properly</strong></p>
                    <p><code>infra.yml</code> is fixed to work on the group named <a
                        href="https://github.com/Vonng/pigsty/blob/master/pigsty.yml#L32"><code>meta</code></a> in the
                        configuration file</p>
                    <p><strong>meta nodes</strong> can be reused as <strong>normal nodes</strong>, i.e. PostgreSQL databases
                        can be defined and created on meta nodes as well.</p>
                    <p>Pigsty recommends using the default configuration and creating a cluster of <a
                        href="https://github.com/Vonng/pigsty/blob/master/pigsty.yml#L43"><code>pg-meta</code></a> meta-databases
                        on the meta-nodes for hosting Pigsty advanced features.</p>
                    <p>A complete execution of the initialization process can take 2 to 8 minutes, depending on the machine
                        configuration.</p>
                    <h2 id="selective-execution">Selective execution</h2>
                    <p>Users can <strong>selectively execute</strong> a subset of scripts through ansible&rsquo;s tagging
                        mechanism.</p>
                    <p>For example, if you want to execute only the local source initialization part, you can do so with the
                        following command.</p>
                    <div className="highlight"><pre><code
                        className="language-bash" data-lang="bash">. /infra.yml --tags<span>=</span>repo
</code></pre>
                    </div>
                    <p>Please refer to [<strong>task details</strong>](#task details) for specific tags</p>
                    <p>Some common subsets of tasks include.</p>
                    <div className="highlight"><pre><code
                        className="language-bash"
                        data-lang="bash">. /infra.yml --tags<span>=</span>repo -e <span>repo_rebuild</span><span>=</span>
                    <span>true</span>
                    <span># Force a local source to be recreated</span>
. /infra.yml --tags<span>=</span>prometheus_reload <span
                        ># Reload Prometheus configuration</span>
. /infra.yml --tags<span>=</span>nginx_haproxy <span
                        ># Regenerate the Nginx Haproxy index page</span>
. /infra.yml --tags<span>=</span>prometheus_targets,prometheus_reload <span
                        ># Regenerate the Prometheus static monitoring object file and apply</span>
</code></pre>
                    </div>
                    <h2 id="script-description">script description</h2>
                    <p><a href="https://github.com/Vonng/pigsty/blob/master/infra.yml"><code>infra.yml</code></a> does the
                        following stuff:</p>
                    <ul>
                        <li>Deploy and enable local sources</li>
                        <li>Complete metanode initialization</li>
                        <li>Complete meta-node infrastructure initialization
                            <ul>
                                <li>CA infrastructure</li>
                                <li>DNS Nameserver</li>
                                <li>Nginx</li>
                                <li>Prometheus &amp; Alertmanger</li>
                                <li>Grafana</li>
                            </ul>
                        </li>
                        <li>Copy the Pigsty ontology to the meta node</li>
                        <li>Complete database initialization on the meta node (optional, users can reuse the meta node
                            through the standard database cluster initialization process)
                        </li>
                    </ul>
                </div>

                <Form className='playbook-item-run-script'>
                    <Form.Field>
                        <label>Run Script</label>
                        <Form.Group inline>
                            <code className='code' style={{width: '12rem'}}>./infra.yml </code>
                            <Input fluid
                                   className='code'
                                   placeholder='--tags='
                                   onChange={(e,{name,value})=>{
                                       this.setState({
                                           cmd:value
                                       })
                                   }}
                            />
                            <Button primary type='submit'
                                    onClick={this.runScript.bind(this)}
                            >Run</Button>
                        </Form.Group>
                    </Form.Field>
                </Form>


            </div>

        );
    }
}

const mapStateToProps = (state, props) => ({});
const mapDispatchToProps = dispatch => ({});

export default connect(mapStateToProps, mapDispatchToProps)(InfraProvision);
