import Axios from "axios";
import eventEmitter from "../commons/eventEmitter";
import {
    getConfigData,
    saveConfigData,
    saveLocalConfigData,

    addNewCluster,
    removeCluster,

    addNewClusterHost,
    removeClusterHost,

    addNewClusterPgUser,
    removeClusterPgUser,

    addNewClusterPgDatabase,
    removeClusterPgDatabase,

    editClusterVars,

    saveLocalClusterConfig,
    saveLocalClusterVarsConfig
} from './configData';

import {
    getCurrentJob,
    createJob,
    deleteJob,
    getJobs,
    getJobLogs
} from './jobs';


Axios.interceptors.request.use(function (config) {
    // Do something before request is sent

    config.timeout = 10000;
    config.headers = {
        'content-type': 'application/x-www-form-urlencoded'
    }
    return config;
}, function (error) {
    // Do something with request error
    eventEmitter.emit('showToasts',{
        type:'error',
        msg:error
    })
    return Promise.reject(error);
});
Axios.interceptors.response.use(function (response) {

    if(response.data.success === false){
        eventEmitter.emit('showToasts',{
            type:'error',
            msg:response.data
        })
    }
    return response
}, function (error) {
    if(error.message === 'Network Error' || error.message.indexOf('timeout') !== -1){
        eventEmitter.emit('showToasts',{
            type:'error',
            msg:error.message
        })
    }else {
        eventEmitter.emit('showToasts',{
            type:'error',
            msg:error.response.data.errorInfo
        })
    }
    return Promise.reject(error)
})

const actions = {
    getConfigData,
    saveConfigData,
    saveLocalConfigData,
    addNewCluster,
    removeCluster,
    addNewClusterHost,
    removeClusterHost,
    addNewClusterPgUser,
    removeClusterPgUser,
    addNewClusterPgDatabase,
    removeClusterPgDatabase,
    editClusterVars,
    saveLocalClusterConfig,
    saveLocalClusterVarsConfig,

    getCurrentJob,
    createJob,
    deleteJob,
    getJobs,
    getJobLogs,
}

export default actions;
