import './App.css';
import {HashRouter, Switch} from 'react-router-dom';
import Wrap from "./pages/wrap";
import AuthorizedRoute from "./authorizedRoute";
import 'moment/locale/zh-cn';

function App() {
  return (
      <HashRouter basename={"/"}>
        <Switch>
          <AuthorizedRoute path={"/"} component={Wrap}/>
        </Switch>
      </HashRouter>
  );
}

export default App;
