import Axios from "axios";
import qs from 'qs';
import Config from '../config';
import ActionTypes from './types';
import yaml from 'js-yaml'
//
export function getCurrentJob(formParams,callBack) {
    return dispatch=>{
        Axios({
            method:'get',
            url:Config.officialServerUrl+'/api/v1/job',
            data:qs.stringify({
                ...formParams
            })
        }).then(data=>{
            dispatch({
                type:ActionTypes.GET_CURRENT_JOB_SUCCESS,
                storeName:'jobs',
                data:data.data,
                ...formParams
            });
            if (typeof callBack === 'function') {
                callBack({
                    success:true,
                    data:data.data,
                })
            }
        }).catch(error=>{
            //debugger;
        })
    }
}

