import React from 'react'
import {connect} from 'react-redux';
import {Button, Icon} from "semantic-ui-react";
import ClusterItem from './clusterItem';
import eventEmitter from "../../commons/eventEmitter";

class DashBoardPage extends React.Component {

    _renderClusters(){
        const {
            configData
        } = this.props;
        let renders = Object.keys(configData.all.children).map((cKeyItem,cKeyIndex)=>{
            let clusterItem = configData.all.children[cKeyItem];
            return <ClusterItem
                key={'key-'+cKeyIndex}
                clusterData={clusterItem}
                clusterName={cKeyItem}
            />
        })

        return renders;

    }

    _addNewCluster(){
        eventEmitter.emit(
            'showEditClusterForm',
            {
                editType:'add',
                callback:(flag)=>{
                    //console.log(flag)
                }
            }
        )
    }

    render() {
        return (
            <React.Fragment>
                <div className='page-wrap'>
                    <div className='flex-row-right mb-1rem'>
                        <Button
                            primary
                            onClick={this._addNewCluster.bind(this)}
                        >
                            <Icon name='plus' />New Cluster
                        </Button>
                    </div>
                    <div>
                        {this._renderClusters()}
                    </div>
                </div>

            </React.Fragment>


        );
    }
}
const mapStateToProps = (state, props) => ({
    //account: state.account,
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
});

export default connect(mapStateToProps, mapDispatchToProps)(DashBoardPage);
