import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Modal} from "semantic-ui-react";
import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/addon/display/autorefresh';
import 'codemirror/addon/comment/comment';
import 'codemirror/addon/edit/matchbrackets';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import 'codemirror/theme/twilight.css';
import './cmdViewer.css';
import eventEmitter from "../commons/eventEmitter";
import actions from "../actions";

class CmdViewer extends React.Component {
    constructor(props) {
        super(props);
        this.cmdUrl = '';
        this.state = {
            open:false,
            renderCode:`Loading...`,
            eventName:'',
            sseUrl:'',
            jobId:''
        }
        this.line = 0;
        this.codeMirrorRef = null;
        this.streamSSE = null;

        this.timer = null;
        this.timerGap = 3000;
    }

    componentDidMount() {

        this.emitter = eventEmitter.addListener('showCmdViewer',(data)=>{
            this.setState({
                open:true,
                renderCode:`Loading...`,
                sseUrl:data.sseUrl,
                eventName:data.eventName,
                jobId:data.jobId
            },()=>{
                //this._openSSE();
                this._startLogTimer();
            })
        })
    }

    _startLogTimer(){
        const {
            jobId
        } = this.state;
        const {
            getJobLogs
        } = this.props;
        let that = this;
        this.timer = setInterval(()=>{
            getJobLogs({
                jobId
            },(backdata)=>{
                that.setState({
                    renderCode:backdata.data
                })
            })
        },this.timerGap)
    }

    closeModal(){
        if(this.streamSSE){
            this.streamSSE.close();
        }
        if(this.timer){
            clearInterval(this.timer)
        }
        this.setState({
            open:false
        })
    }

    _openSSE(){
        const {
            eventName,
            sseUrl
        } = this.state;
        let that = this;
        if(this.streamSSE){
            this.streamSSE.close();
        }
        this.streamSSE = new EventSource(sseUrl);
        this.streamSSE?.addEventListener(eventName,function (e){
            that.setState({
                renderCode:that.state.renderCode+e.data
            },()=>{
                if(that.codeMirrorRef.editor){
                    that.codeMirrorRef.editor.scrollTo(
                        0,
                        that.codeMirrorRef.editor.getScrollInfo().height
                    )
                }
            })
        })
    }

    stopCmd(){
        const {
            jobId
        } = this.state;
        const {
            deleteJob
        } = this.props;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'Are you sure stop this script ?',
                confirmButton: 'Stop',
                callback: (flag) => {
                    if (flag) {
                        deleteJob({
                            jobid:jobId,
                        },backdata=>{
                            if(backdata.data.message === 'job deleted'){
                                eventEmitter.emit('showToasts',{
                                    type:'success',
                                    msg:'save success'
                                })
                            }else {
                                eventEmitter.emit('showToasts',{
                                    type:'error',
                                    msg:backdata.data.message
                                })
                            }
                        })

                    }
                }
            }
        )
    }

    render() {

        const {
            renderCode,
            open
        } = this.state;
        return (
            <Modal
                closeIcon
                open={open}
                centered={false}
                className={'modal-cmdviewer'}
                onClose={this.closeModal.bind(this)}
            >
                <Modal.Header>Console Logs</Modal.Header>
                <Modal.Content scrolling>
                    <div className="cmdViewer">
                        <CodeMirror
                            ref={(ref)=>{
                                this.codeMirrorRef = ref
                            }}
                            value={renderCode}
                            options={{
                                theme: 'monokai',
                                tabSize: 2,
                                keyMap: 'sublime',
                                mode: 'shell',
                                readOnly:'nocursor',
                                lineNumbers:false,
                                autofocus:false
                            }}
                            // onUpdate={(instance)=>{
                            //     setTimeout(()=>{
                            //         console.log(instance.getScrollInfo().height)
                            //         instance.scrollTo({
                            //             x:0,
                            //             y:instance.getScrollInfo().height
                            //         })
                            //     },200)
                            // }}

                        />

                    </div>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                            onClick={this.stopCmd.bind(this)}
                    >
                        Stop
                    </Button>
                    <Button primary
                        onClick={this.closeModal.bind(this)}
                    >
                        Close
                    </Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({
    jobs:state.jobs
});
const mapDispatchToProps = dispatch => ({
    getJobLogs: (formParams, callBack) => dispatch(actions.getJobLogs(formParams, callBack)),
    deleteJob: (formParams, callBack) => dispatch(actions.deleteJob(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CmdViewer));
