import React from 'react'
import {connect} from 'react-redux';
import {Tab} from "semantic-ui-react";
import './index.css';
import actions from "../../actions";
import CurrentJobLog from "./currentJobLog";
import HistoryJobs from "./historyJobs";

const panes = [
    {
        menuItem: { key: 'currentJobLog', icon: 'arrow alternate circle right outline', content: 'Current Job' },
        render: () => <Tab.Pane className='job-pane'><CurrentJobLog/></Tab.Pane> },
    {
        menuItem: { key: 'historyJobs', icon: 'arrow alternate circle right outline', content: 'History Jobs' },
        render: () => <Tab.Pane className='job-pane'><HistoryJobs/></Tab.Pane> }
]

class JobsPage extends React.Component {

    constructor(props) {
        super(props);
    }

    _renderJobs(){
        const {
            configData
        } = this.props;
        let renders = Object.keys(configData.all.children).map((cKeyItem,cKeyIndex)=>{
            let clusterItem = configData.all.children[cKeyItem];
            return null
        })

        return renders;

    }



    componentDidMount() {

    }

    render() {
        return (
            <div className='page-wrap jobs'>
                <Tab menu={{ fluid: true, vertical: true, tabular: true }} panes={panes} />
            </div>
        );
    }
}
const mapStateToProps = (state, props) => ({
    configData:state.configData,
    jobs:state.jobs
});
const mapDispatchToProps = dispatch => ({
    getJobs: (formParams, callBack) => dispatch(actions.getJobs(formParams, callBack)),
});

export default connect(mapStateToProps, mapDispatchToProps)(JobsPage);
