import React from 'react'
import Header from "../components/header";
import Footer from "../components/footer";
import {Route} from "react-router-dom";
import DashBoard from "./dashBoard";
import PlaybookPage from "./playbook";
import PigstyConfigPage from './pigstyConfig';
import JobsPage from './jobs';
import CmdViewer from '../components/cmdViewer';
import CodeEditor from "../components/codeEditor";
import EditClusterForm from "../components/forms/editClusterForm";
import EditClusterHostForm from '../components/forms/editClusterHostForm';
import EditClusterPgUserForm from "../components/forms/editClusterPgUserForm";
import EditClusterPgDatabaseForm from "../components/forms/editClusterPgDatabaseForm";

class Wrap extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="inner-page">
                    <Header/>
                    <div id="mainWrap">
                        <div id="mainContent">
                            <Route path={"/"} exact component={DashBoard}/>
                            <Route path={"/config"} component={PigstyConfigPage}/>
                            <Route path={"/playbook"} component={PlaybookPage}/>
                            <Route path={"/jobs"} component={JobsPage}/>
                        </div>
                    </div>
                    <Footer/>
                </div>
                <CmdViewer />
                <CodeEditor/>
                <EditClusterForm/>
                <EditClusterHostForm/>
                <EditClusterPgUserForm/>
                <EditClusterPgDatabaseForm/>
            </React.Fragment>
        );
    }
}

export default Wrap;
