import ActionTypes from '../actions/types';
const defaultState = {
}

export default function onAction (state = defaultState,action){
    switch (action.type){
        case ActionTypes.GET_CONFIG_DATA_SUCCESS:
            return action.data;
        case ActionTypes.SAVE_LOCAL_CONFIG_DATA_SUCCESS:
            return action.data;
        case ActionTypes.ADD_NEW_CLUSTER_SUCCESS:
            state.all.children[action.clusterName] = {
                edit:true,
                hosts:{},
                vars:{
                    pg_cluster:action.clusterName,
                    pg_users:[],
                    pg_databases:[]
                },

            };
            return JSON.parse(JSON.stringify(state));
        case ActionTypes.REMOVE_CLUSTER_SUCCESS:
            delete state.all.children[action.clusterName];
            return JSON.parse(JSON.stringify(state));
        case ActionTypes.ADD_NEW_CLUSTER_HOST_SUCCESS:
            state.all.children[action.clusterName].edit = true;
            state.all.children[action.clusterName].hosts[action.host_ip] = {
                pg_offline_query:action.pg_offline_query,
                pg_role:action.pg_role,
                pg_seq:action.pg_seq,
            }
            return JSON.parse(JSON.stringify(state));
        case ActionTypes.REMOVE_CLUSTER_HOST_SUCCESS:
            state.all.children[action.clusterName].edit = true;
            delete state.all.children[action.clusterName].hosts[action.hostName];
            return JSON.parse(JSON.stringify(state));

        case ActionTypes.ADD_NEW_CLUSTER_PG_USER_SUCCESS:
            if(!state.all.children[action.clusterName]?.vars){
                state.all.children[action.clusterName].vars = {};
            }
            if(!state.all.children[action.clusterName]?.vars?.pg_users){
                state.all.children[action.clusterName].vars.pg_users = [];
            }
            if(state.all.children[action.clusterName]?.vars?.pg_users){
                let pgUser = action.formData;

                state.all.children[action.clusterName].vars.pg_users.push(
                    pgUser
                );
                state.all.children[action.clusterName].edit = true;
            }
            return JSON.parse(JSON.stringify(state));

        case ActionTypes.REMOVE_CLUSTER_PG_USER_SUCCESS:
            if(state.all.children[action.clusterName]?.vars?.pg_users){
                let fIndex = state.all.children[action.clusterName].vars.pg_users.findIndex(item => item.name === action.name)
                state.all.children[action.clusterName].vars.pg_users.splice(fIndex, 1);
                state.all.children[action.clusterName].edit = true;
            }
            return JSON.parse(JSON.stringify(state));


        case ActionTypes.ADD_NEW_CLUSTER_PG_DATABASE_SUCCESS:
            if(!state.all.children[action.clusterName]?.vars){
                state.all.children[action.clusterName].vars = {};
            }
            if(!state.all.children[action.clusterName]?.vars?.pg_databases){
                state.all.children[action.clusterName].vars.pg_databases = [];
            }
            if(state.all.children[action.clusterName]?.vars?.pg_databases){
                let pgDatabase = action.formData;

                state.all.children[action.clusterName].vars.pg_databases.push(
                    pgDatabase
                );
                state.all.children[action.clusterName].edit = true;
            }
            return JSON.parse(JSON.stringify(state));

        case ActionTypes.REMOVE_CLUSTER_PG_DATABASE_SUCCESS:
            if(state.all.children[action.clusterName]?.vars?.pg_databases){
                let fIndex = state.all.children[action.clusterName].vars.pg_databases.findIndex(item => item.name === action.name)
                state.all.children[action.clusterName].vars.pg_databases.splice(fIndex, 1);
                state.all.children[action.clusterName].edit = true;
            }
            return JSON.parse(JSON.stringify(state));

        case ActionTypes.SAVE_LOCAL_CLUSTER_VARS_CONFIG_SUCCESS:
            state.all.children[action.clusterName].vars = action.configData;
            return JSON.parse(JSON.stringify(state));

        case ActionTypes.SAVE_LOCAL_CLUSTER_CONFIG_SUCCESS:
            state.all.children[action.clusterName] = action.configData;
            return JSON.parse(JSON.stringify(state));

        default:
            return state;
    }
}
