import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button,Divider,Icon} from "semantic-ui-react";
import CodeMirror from '@uiw/react-codemirror';
import 'codemirror/addon/display/autorefresh';
import 'codemirror/addon/comment/comment';
import 'codemirror/addon/edit/matchbrackets';
import 'codemirror/keymap/sublime';
import 'codemirror/theme/monokai.css';
import './codeViewer.css';
import yaml from 'js-yaml'
import actions from "../actions";
import eventEmitter from "../commons/eventEmitter";
class CodeViewer extends React.Component {
    constructor(props) {
        super(props);
        this.codeM = null;
    }

    _goToPlaybook(){
        const {
            history,
        } = this.props;
        history.push('/playbook');
    }

    _saveYaml(){
        const {
            saveLocalConfigData,
            saveConfigData
        } = this.props;

        let JSONYaml = null;
        try {
            JSONYaml = yaml.load(this.codeM.editor.getValue());
        } catch (e) {
            eventEmitter.emit('showToasts',{
                type:'error',
                msg:e.toString()
            })
        }
        if(JSONYaml){
            saveLocalConfigData({
                data:JSONYaml
            },(backData)=>{
                if(backData.success){
                    let newJSONYaml = JSON.parse(JSON.stringify(JSONYaml));
                    Object.keys(newJSONYaml.all.children).map((rCItem)=>{
                        delete newJSONYaml.all.children[rCItem].edit
                    })
                    saveConfigData(yaml.dump(newJSONYaml),sbackData=>{
                        //console.log(sbackData);
                        if(sbackData.data.message === 'ok'){
                            eventEmitter.emit('showToasts',{
                                type:'success',
                                msg:'save success'
                            })
                        }else {
                            eventEmitter.emit('showToasts',{
                                type:'error',
                                msg:sbackData.data.message
                            })
                        }

                    })
                }

            })
        }
    }

    render() {
        const {
            configData
        } = this.props;
        let that = this;
        let renderConfigData = JSON.parse(JSON.stringify(configData));
        Object.keys(renderConfigData.all.children).map((rCItem)=>{
            delete renderConfigData.all.children[rCItem].edit
        })
        let yamlCode = yaml.dump(renderConfigData);
        //console.log(yamlCode)
        return (
            <div>
                <div className="codeViewer">
                    <CodeMirror
                        ref={_ref=>{
                            if(_ref?.editor){
                                //if(_ref.hasOwnProperty('editor'))
                                that.codeM = _ref;
                            }
                        }}
                        value={yamlCode}
                        options={{
                            theme: 'monokai',
                            tabSize: 2,
                            keyMap: 'sublime',
                            mode: 'yaml',
                            //readOnly:'nocursor',
                            //autofocus:false
                        }}
                    />
                </div>
                <Divider/>
                <div>
                    <Button primary onClick={this._saveYaml.bind(this)}>Save YAML</Button>
                    <Button onClick={this._goToPlaybook.bind(this)}><Icon name='paper plane outline' />Go to Playbook</Button>
                </div>

            </div>

        )
    }
}

const mapStateToProps = (state, props) => ({
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
    saveLocalConfigData: (formParams, callBack) => dispatch(actions.saveLocalConfigData(formParams, callBack)),
    saveConfigData: (formParams, callBack) => dispatch(actions.saveConfigData(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CodeViewer));
