import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../actions";
import {Button, Icon, List} from 'semantic-ui-react';
import eventEmitter from "../../commons/eventEmitter";

class ClusterPgUserItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    _removeClusterPgUser(name) {
        const {
            removeClusterPgUser,
            clusterName
        } = this.props;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'Are you sure ?',
                callback: (flag) => {
                    if (flag) {
                        removeClusterPgUser({
                            name,
                            clusterName
                        }, backData => {

                        })
                    }
                }
            }
        )


    }

    render() {
        const {
            pgUser,
        } = this.props;
        return (
            <List.Item className='mb-5px'>
                <Button size='mini' icon='trash' basic className='right floated no-shadow'
                        onClick={this._removeClusterPgUser.bind(this, pgUser.name)}
                />
                <Icon
                    name='user'
                    className='left floated'
                    fitted
                    size='large'
                />
                <List.Content>
                    <List.Header as='a'>{pgUser.name}</List.Header>
                    <List.Description className='mt-5px'>
                        {pgUser.comment}
                    </List.Description>
                </List.Content>
            </List.Item>
        )
    }
}

const mapStateToProps = (state, props) => ({

});
const mapDispatchToProps = dispatch => ({
    removeClusterPgUser: (formParams, callBack) => dispatch(actions.removeClusterPgUser(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ClusterPgUserItem));
