import React from 'react';
import eventEmitter from "../commons/eventEmitter";
import {Message} from "semantic-ui-react";

const toastTypeClassName = {
    info:"toast-primary",
    success:"toast-success",
    warning:"toast-warning",
    error:"toast-error"
}

class ToastsPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toasts:[]
        }
    }

    _delItem(){
        const {
            toasts
        } = this.state;
        toasts.shift();
        this.setState({
            toasts:toasts
        })
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showToasts',(data)=>{
            this.setState({
                toasts:[
                    ...this.state.toasts,
                    data
                ]
            },()=>{
                setTimeout(()=>{
                    this._delItem();
                },3000)
            })
        })
    }

    componentWillUnmount() {
        //eventEmitter.removeListener(this.emitter);
    }

    _renderToast(tItem,tIndex){
        return (
            <Message
                className='toast'
                {...{[tItem.type]:tItem.type}}
                key={tIndex}
                header={tItem.msg}
            />
        )
    }

    render() {

        const {
            toasts
        } = this.state;
        let renders = [];
        toasts.forEach((tItem,tIndex)=>{
            renders.push(this._renderToast(tItem,tIndex))
        })
        return (
            <div id="toastsPanel">
                {renders}
            </div>
        );
    }
}

export default ToastsPanel;
