import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Card, Form, Icon, Input, Label, List, Modal} from "semantic-ui-react";
import eventEmitter from "../../commons/eventEmitter";
import actions from "../../actions";

class EditClusterPgUserForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            editType: 'add',//add || edit

            name: '',
            password: '',
            login: null,
            superuser: null,
            createdb: null,
            createrole: null,
            inherit: null,
            replication: null,
            bypassrls: null,
            connlimit: -1,
            expire_at: '2030-12-31',
            parameters: {
                search_path: 'public'
            },
            roles: ['dbrole_offline'],
            expire_in: 365,
            pgbouncer: null,
            comment: '',

            clusterData: null,

            parameterKey:'',
            parameterValue:''
        }
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showEditClusterPgUserForm', (data) => {
            this.setState({
                open: true,
                ...data
            })
        })
    }

    setOpen(flag) {
        this.setState({
            open: flag
        })
    }

    _inputHandle(name, value) {
        this.setState({
            [name]: value
        })
    }

    _addParameter(){
        const {
            parameterKey,
            parameterValue
        } = this.state;
        if(parameterKey.length > 0 && parameterValue.length >0){
            this.setState({
                parameters:{
                    ...this.state.parameters,
                    [parameterKey]:parameterValue
                },
                parameterKey:'',
                parameterValue:''
            })
        }

    }

    _deleteParameter(parameterKey){
        delete this.state.parameters[parameterKey];
        this.setState({
            parameters:{
                ...this.state.parameters
            }
        })
    }

    _checkboxHandle(name, value, flag) {

        if (flag) {
            this.setState({
                [name]: [...this.state[name], value]
            })
        } else {
            this.state[name].splice(this.state[name].findIndex(item => item === value), 1)
            this.setState({
                [name]: this.state[name]
            })
        }
    }

    _editClusterPgUser() {

    }

    _addNewClusterPgUser() {
        const {
            addNewClusterPgUser
        } = this.props;
        const {
            name,
            password,
            login,
            superuser,
            createdb,
            createrole,
            inherit,
            replication,
            bypassrls,
            connlimit,
            expire_at,
            parameters,
            roles,
            expire_in,
            pgbouncer,
            comment,
            clusterName,
        } = this.state;

        let fromData = {
            name,
            password,
            roles,
            parameters,
            expire_at,
            //expire_in,
            connlimit,
        }
        if(comment.length > 0){
            fromData.comment = comment
        }
        if(login != null){
            fromData.login = login
        }
        if(superuser != null){
            fromData.superuser = superuser
        }
        if(createdb != null){
            fromData.createdb = createdb
        }
        if(createrole != null){
            fromData.createrole = createrole
        }
        if(inherit != null){
            fromData.inherit = inherit
        }
        if(replication != null){
            fromData.replication = replication
        }
        if(bypassrls != null){
            fromData.bypassrls = bypassrls
        }
        if(pgbouncer != null){
            fromData.pgbouncer = pgbouncer
        }

        //
        addNewClusterPgUser({
            formData: fromData,
            clusterName
        }, backData => {
            if(backData.success){
                this.setState({
                    open: false
                })
            }
        })
    }


    render() {
        const {
            open,
            editType,
            name,
            password,
            login,
            superuser,
            createdb,
            createrole,
            inherit,
            replication,
            bypassrls,
            connlimit,
            expire_at,
            parameters,
            roles,
            expire_in,
            pgbouncer,
            comment,
            parameterKey,
            parameterValue
        } = this.state;


        let _renderParameters = Object.keys(parameters).map((pItem,pIndex)=>{
            return (
                <Form.Group widths='equal' className='m' key={'key'+pIndex}>
                    <Form.Input value={pItem} placeholder='key'
                                disabled
                                style={{color:'#333'}}
                    />
                    <Form.Input value={parameters[pItem]} placeholder='value'
                                disabled
                                style={{color:'#333'}}
                    />
                    <Button type='submit' icon='minus'
                            onClick={this._deleteParameter.bind(this,pItem)}
                    ></Button>
                </Form.Group>
            )
        })

        return (
            <Modal
                onClose={this.setOpen.bind(this, false)}
                open={open}
                size='small'
            >
                <Modal.Header>{'Manage Cluster Pg Users'}</Modal.Header>
                <Modal.Content>
                    <Form>
                        <Form.Group widths='equal' className='border-bottom pb-5px'>
                            <Form.Field>
                                <label>name</label>
                                <Input
                                    placeholder=''
                                    name='name'
                                    value={name}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>
                            <Form.Field>
                                <label>password</label>
                                <Input
                                    placeholder=''
                                    name='password'
                                    value={password}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>
                            <Form.Field>
                                <label>comment</label>
                                <Input
                                    placeholder='example personal user for interactive queries'
                                    name='comment'
                                    value={comment}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>
                        </Form.Group>
                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>login</label>
                                <Form.Radio
                                    label='null'
                                    name='login'
                                    checked={login === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='login'
                                    checked={login === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='login'
                                    checked={login === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                            <Form.Group inline className='flex-1'>
                                <label>superuser</label>
                                <Form.Radio
                                    label='null'
                                    name='superuser'
                                    checked={superuser === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='superuser'
                                    checked={superuser === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='superuser'
                                    checked={superuser === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>
                        </Form.Group>
                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>createdb</label>
                                <Form.Radio
                                    label='null'
                                    name='createdb'
                                    checked={createdb === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='createdb'
                                    checked={createdb === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='createdb'
                                    checked={createdb === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                            <Form.Group inline className='flex-1'>
                                <label>createrole</label>
                                <Form.Radio
                                    label='null'
                                    name='createrole'
                                    checked={createrole === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='createrole'
                                    checked={createrole === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='createrole'
                                    checked={createrole === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>
                        </Form.Group>
                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>inherit</label>
                                <Form.Radio
                                    label='null'
                                    name='inherit'
                                    checked={inherit === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='inherit'
                                    checked={inherit === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='inherit'
                                    checked={inherit === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                            <Form.Group inline className='flex-1'>
                                <label>replication</label>
                                <Form.Radio
                                    label='null'
                                    name='replication'
                                    checked={replication === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='replication'
                                    checked={replication === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='replication'
                                    checked={replication === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>
                        </Form.Group>
                        <Form.Group widths='equal' className='border-bottom plr-05rem pb-5px'>
                            <Form.Group inline className='flex-1'>
                                <label>bypassrls</label>
                                <Form.Radio
                                    label='null'
                                    name='bypassrls'
                                    checked={bypassrls === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='bypassrls'
                                    checked={bypassrls === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='bypassrls'
                                    checked={bypassrls === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>

                            <Form.Group inline className='flex-1'>
                                <label>pgbouncer</label>
                                <Form.Radio
                                    label='null'
                                    name='pgbouncer'
                                    checked={pgbouncer === null}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, null)
                                    }}
                                />
                                <Form.Radio
                                    label='true'
                                    name='pgbouncer'
                                    checked={pgbouncer === true}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, true)
                                    }}
                                />
                                <Form.Radio
                                    label='false'
                                    name='pgbouncer'
                                    checked={pgbouncer === false}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, false)
                                    }}
                                />
                            </Form.Group>
                        </Form.Group>

                        <Form.Field>
                            <label>roles (Separated by commas)</label>
                            <Input
                                placeholder='role,role'
                                name='roles'
                                value={roles.join(',')}
                                onChange={(e, {name, value}) => {
                                    this._inputHandle(name, value.split(','))
                                }}
                            />
                        </Form.Field>

                        <Form.Group widths='equal'>
                            <Form.Field>
                                <label>connlimit</label>
                                <Input
                                    placeholder=''
                                    name='connlimit'
                                    value={connlimit}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>

                            <Form.Field>
                                <label>expire_at</label>
                                <Input
                                    placeholder=''
                                    name='expire_at'
                                    value={expire_at}
                                    onChange={(e, {name, value}) => {
                                        this._inputHandle(name, value)
                                    }}
                                />
                            </Form.Field>



                        </Form.Group>

                        <Form.Field>
                            <label>parameters</label>
                            <Form.Group widths='equal'>
                                <Form.Input name='parameterKey' value={parameterKey} placeholder='key'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Form.Input name='parameterValue' value={parameterValue} placeholder='value'
                                            onChange={(e, {name, value}) => {
                                                this._inputHandle(name, value)
                                            }}
                                />
                                <Button type='submit' icon='plus'
                                        onClick={this._addParameter.bind(this)}
                                ></Button>
                            </Form.Group>
                            <div>
                                {_renderParameters}
                            </div>
                        </Form.Field>



                    </Form>
                </Modal.Content>
                <Modal.Actions>
                    <Button
                        onClick={this.setOpen.bind(this, false)}
                    >Cancel</Button>
                    <Button
                        primary
                        onClick={this._addNewClusterPgUser.bind(this)}
                    >Save</Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({
});
const mapDispatchToProps = dispatch => ({
    addNewClusterPgUser: (formParams, callBack) => dispatch(actions.addNewClusterPgUser(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditClusterPgUserForm));
