import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../actions";
import { Button, List,Icon,Label } from 'semantic-ui-react';
import eventEmitter from "../../commons/eventEmitter";

class ClusterHostItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    _removeClusterHost(){
        const {
            removeClusterHost,
            clusterName,
            hostName
        } = this.props;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'It will remove this cluster host !',
                callback:(flag)=>{
                    if(flag){
                        removeClusterHost({
                            clusterName,
                            hostName:hostName
                        },callback=>{

                        })
                    }
                }
            }
        )
    }

    render() {
        const {
            hostData,
            hostName
        } = this.props;
        let descs = [];
        if(hostData){
            descs = Object.keys(hostData).map((hKeyItem,hKeyIndex)=>{
                return (
                    <Label key={'k-'+hKeyIndex}>
                        {hKeyItem}:
                        <Label.Detail>{hostData[hKeyItem].toString()}</Label.Detail>
                    </Label>
                )
            })
        }
        return (
            <List.Item className='mb-1rem'>
                <Button size='mini' icon='trash' basic className='right floated no-shadow'
                    onClick={this._removeClusterHost.bind(this)}
                />
                <Icon
                    name='hdd outline'
                    className='left floated'
                    fitted
                    size='large'
                />
                <List.Content>
                    <List.Header as='a'>{hostName}</List.Header>
                    <List.Description className='mt-5px'>
                        {descs}
                    </List.Description>
                </List.Content>
            </List.Item>
        )
    }
}

const mapStateToProps = (state, props) => ({

});
const mapDispatchToProps = dispatch => ({
    removeClusterHost: (formParams, callBack) => dispatch(actions.removeClusterHost(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ClusterHostItem));
