import React from 'react';
import eventEmitter from "../commons/eventEmitter";
class LoadingCover extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible:props.visible?props.visible:false,
            loadingText:props.loadingText?props.loadingText:''
        }
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showLoadingCover',(data)=>{
            this.setState({
                visible:data,
                loadingText:'...'
            })
        })
    }

    componentWillUnmount() {

    }

    render() {
        const {
            visible,
            loadingText
        } = this.state;
        if(visible){
            return (
                <div id="loadingCover">
                    <div className="loading-overlay">
                        <div className="loading loading-lg"></div>
                        <div className="font-bold" style={{marginLeft:'2rem'}}>{loadingText}</div>
                    </div>
                </div>
            );
        }else {
            return null
        }
    }
}

export default LoadingCover;
