import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Form, Input, Modal} from "semantic-ui-react";
import eventEmitter from "../../commons/eventEmitter";
import actions from "../../actions";

class EditClusterForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open:false,
            editType:'add',//add || edit
            pg_offline_query:false,
            host_ip:'',
            pg_role:'',
            pg_seq:1,
            clusterName:null,
            clusterData:null
        }

        this.roleOptions = []
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showEditClusterHostForm', (data) => {

            this.roleOptions = [
                { key: 'p', text: 'primary', value: 'primary' },
                { key: 'r', text: 'replica', value: 'replica' },
                { key: 'o', text: 'offline', value: 'offline' },
                { key: 's', text: 'standby', value: 'standby' },
                { key: 'd', text: 'delayed', value: 'delayed' }
            ]
            let roleValue = 'primary';
            if(Object.keys(data.clusterData.hosts).length != 0){
                Object.keys(data.clusterData.hosts).forEach((hItem,hIndex)=>{
                    if(data.clusterData.hosts[hItem]?.pg_role === 'primary'){
                        roleValue = 'replica';
                    }
                })
            }

            this.setState({
                open: true,
                ...data,
                pg_role:roleValue
            })
        })
    }

    setOpen(flag){
        this.setState({
            open:flag
        })
    }

    _inputHandle(name,value){
        this.setState({
            [name]:value
        })
    }

    _editClusterHost(){
        const {
            addNewClusterHost
        } = this.props;
        const {
            pg_offline_query,
            host_ip,
            pg_role,
            pg_seq,
            clusterName
        } = this.state;

        if(clusterName){
            addNewClusterHost({
                pg_offline_query,
                host_ip,
                pg_role,
                pg_seq,
                clusterName
            },(callback)=>{
                this.setOpen(false)
            })
        }
    }


    render() {
        const {
            open,
            pg_offline_query,
            host_ip,
            pg_role,
            pg_seq,
            editType,
            clusterData
        } = this.state;
        const {
            configData
        } = this.props;

        if(!open){
            return null;
        }




        return (
            <Modal
                onClose={this.setOpen.bind(this,false)}
                open={open}
                size='mini'
            >
                <Modal.Header>{editType === 'add'?'Add Cluster Host':'Edit Cluster Host'}</Modal.Header>
                <Modal.Content>
                    <Form>
                        <Form.Field>
                            <label>Node IP (Inventory Hostname)</label>
                            <Input
                                placeholder='0.0.0.0'
                                name='host_ip'
                                value={host_ip}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,value)
                                }}
                            />
                        </Form.Field>
                        <Form.Field>
                            <label>Instance Sequence</label>
                            <Input
                                placeholder='1'
                                name='pg_seq'
                                value={pg_seq}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,parseInt(value))
                                }}
                            />
                        </Form.Field>
                        <Form.Group inline>
                            <label>Offline Query</label>
                            <Form.Radio
                                label='true'
                                name='pg_offline_query'
                                checked={pg_offline_query === true}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,true)
                                }}
                            />
                            <Form.Radio
                                label='false'
                                name='pg_offline_query'
                                checked={pg_offline_query === false}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,false)
                                }}
                            />
                        </Form.Group>
                        <Form.Field>
                            <Form.Select
                                fluid
                                label='Role'
                                options={this.roleOptions}
                                name='pg_role'
                                placeholder=''
                                value={pg_role}
                                onChange={(e,{name,value})=>{
                                    this._inputHandle(name,value)
                                }}
                            />
                        </Form.Field>
                    </Form>
                </Modal.Content>
                <Modal.Actions>
                    <Button basic
                            onClick={this.setOpen.bind(this,false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        primary
                        onClick={this._editClusterHost.bind(this)}
                    >Save</Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
    addNewClusterHost: (formParams, callBack) => dispatch(actions.addNewClusterHost(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditClusterForm));
