import React from 'react';
import {Message} from "semantic-ui-react";
export default class NoResultTips extends React.Component {
    render() {
        const {
            title,
            desc,
            buttonText,
            buttomCallback,
            icon
        } = this.props
        return (
            <Message warning
            >
                {title?(
                    <Message.Header>{title}</Message.Header>
                ):null}
                <p>{desc}</p>
            </Message>
        );
    }
}

