import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {Button, Form, Modal} from "semantic-ui-react";
import eventEmitter from "../../commons/eventEmitter";
import actions from "../../actions";

class EditClusterForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false,
            editType: 'add',//add||edit
            clusterName: ''
        }
    }

    componentDidMount() {
        this.emitter = eventEmitter.addListener('showEditClusterForm', (data) => {
            this.setState({
                open: true,
                ...data
            })
        })
    }

    _editCluster(){
        const {
            addNewCluster
        } = this.props;
        const {
            clusterName
        } = this.state;

        if(clusterName.length === 0 || clusterName.length > 50){
            eventEmitter.emit('showToasts',{
                type:'error',
                msg:'cluster name length error'
            })
            return false;
        }

        addNewCluster({
            clusterName
        },(backData)=>{
            this.setOpen(false)
        })
    }

    setOpen(flag) {
        this.setState({
            open: flag,
            clusterName:'',
            editType:'add'
        })
    }

    _inputChange(name, value) {
        this.setState({
            [name]:value
        })
    }


    render() {

        const {
            open,
            clusterName,
            editType
        } = this.state;

        return (
            <Modal
                onClose={this.setOpen.bind(this, false)}
                open={open}
                size='mini'
            >
                <Modal.Header>{editType === 'add'?'Add Cluster':'Edit Cluster'}</Modal.Header>
                <Modal.Content>
                    <Form>
                        <Form.Field>
                            <label>Cluster Name</label>
                            <Form.Input
                                value={clusterName}
                                placeholder='name'
                                name='clusterName'
                                onChange={(e, {name, value}) => {
                                    this._inputChange(name, value)
                                }}
                            />
                        </Form.Field>
                    </Form>
                </Modal.Content>
                <Modal.Actions>
                    <Button basic
                        onClick={this.setOpen.bind(this,false)}
                    >
                        Cancel
                    </Button>
                    <Button
                        primary
                        onClick={this._editCluster.bind(this)}
                    >Save</Button>
                </Modal.Actions>
            </Modal>

        )
    }
}

const mapStateToProps = (state, props) => ({});
const mapDispatchToProps = dispatch => ({
    addNewCluster: (formParams, callBack) => dispatch(actions.addNewCluster(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(EditClusterForm));
