import React from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import actions from "../../actions";
import {Button, Card, Icon, List, Label, Header, Divider, Popup} from 'semantic-ui-react';
import ClusterHostItem from "./clusterHostItem";
import NoResultTips from "../../components/noResultTips";
import eventEmitter from "../../commons/eventEmitter";
import Config from "../../config";
import ClusterPgUserItem from "./clusterUserItem";
import ClusterPgDatabaseItem from "./clusterPgDatabaseItem";
import yaml from 'js-yaml'
import {saveLocalClusterConfig, saveLocalClusterVarsConfig} from "../../actions/configData";

class ClusterItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    _renderHosts() {
        const {
            clusterData,
            clusterName
        } = this.props;

        let renders = [];
        if (clusterData?.hosts) {
            renders = Object.keys(clusterData?.hosts).map((cItem, cIndex) => {
                return <ClusterHostItem
                    key={'key-' + cIndex}
                    clusterName={clusterName}
                    hostName={cItem}
                    hostData={clusterData.hosts[cItem]}
                />
            });
        }

        if (renders.length === 0) {
            return <NoResultTips desc={'No hosts'}/>
        }

        return (
            <List>
                {renders}
            </List>
        );

    }


    _addNewClusterHost() {
        const {
            clusterName,
            clusterData
        } = this.props;
        eventEmitter.emit(
            'showEditClusterHostForm',
            {
                editType: 'add',
                clusterName,
                clusterData,
                callback: (flag) => {
                    //console.log(flag)
                }
            }
        )
    }


    _renderPgUsers() {
        const {
            clusterData,
            clusterName
        } = this.props;

        let renders = [];
        if (clusterData?.vars?.pg_users) {
            renders = clusterData.vars.pg_users.map((pItem, pIndex) => {
                return (
                    <ClusterPgUserItem
                        key={'key-' + pIndex}
                        clusterName={clusterName}
                        pgUser={pItem}
                    />
                )
            })
        }

        if (renders.length === 0) {
            return <NoResultTips desc={'No Pg user'}/>
        }

        return (
            <List>
                {renders}
            </List>
        );

    }

    _manageClusterPgUser() {
        const {
            clusterName,
            clusterData
        } = this.props;
        eventEmitter.emit(
            'showEditClusterPgUserForm',
            {
                clusterName,
                clusterData,
                callback: (flag) => {
                    //console.log(flag)
                }
            }
        )
    }


    _renderPgDatabases() {
        const {
            clusterData,
            clusterName
        } = this.props;

        let renders = [];
        if (clusterData?.vars?.pg_databases) {
            renders = clusterData.vars.pg_databases.map((pItem, pIndex) => {
                return (
                    <ClusterPgDatabaseItem
                        key={'key-' + pIndex}
                        clusterName={clusterName}
                        pgDatabase={pItem}
                    />
                )
            })
        }

        if (renders.length === 0) {
            return <NoResultTips desc={'No Pg databases'}/>
        }

        return (
            <List>
                {renders}
            </List>
        );

    }

    _addNewClusterPgDatabase(){
        const {
            clusterName,
            clusterData
        } = this.props;
        eventEmitter.emit(
            'showEditClusterPgDatabaseForm',
            {
                clusterName,
                clusterData,
                callback: (flag) => {
                    //console.log(flag)
                }
            }
        )
    }

    _runScript() {
        const {
            clusterName,
            createJob
        } = this.props;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'Are you sure run this script ?',
                confirmButton: 'Run',
                callback: (flag) => {
                    if (flag) {
                        createJob({
                            cmd:'?playbook=pgsql&cluster='+clusterName,
                            playbook:'pgsql',
                            cluster:clusterName
                        },backdata=>{
                            //console.log(backdata)
                            eventEmitter.emit(
                                'showCmdViewer',
                                {
                                    jobId:backdata.data.data.id,
                                    eventName: clusterName
                                }
                            )
                        })

                    }
                }
            }
        )

    }

    _removeCluster() {
        const {
            clusterName,
            removeCluster,

        } = this.props;
        let that = this;

        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'It will remove this cluster !',
                callback: (flag) => {
                    if (flag) {
                        removeCluster({
                            clusterName
                        },backdata=>{
                            if(backdata.success){
                                that._saveAllYaml()
                            }
                        })
                    }
                }
            }
        )
    }

    _editClusterVars(){
        const {
            clusterData,
            clusterName,
            saveLocalClusterVarsConfig
        } = this.props;
        eventEmitter.emit(
            'showCodeEditor',
            {
                title: clusterName+ '.vars',
                codeData:yaml.dump(clusterData.vars),
                callback: (backdata) => {
                    let JSONYaml = null;
                    try {
                        JSONYaml = yaml.load(backdata);
                    } catch (e) {
                        eventEmitter.emit('showToasts',{
                            type:'error',
                            msg:e.toString()
                        })
                    }
                    if(JSONYaml){
                        saveLocalClusterVarsConfig({
                            configData:JSONYaml,
                            clusterName
                        },sbackdata=>{
                            if(sbackdata.success){
                                eventEmitter.emit('showToasts',{
                                    type:'success',
                                    msg:'save success'
                                })
                            }
                        })
                    }
                }
            }
        )
    }

    _editClusterYaml(){
        const {
            clusterData,
            clusterName,
            saveLocalClusterConfig
        } = this.props;
        eventEmitter.emit(
            'showCodeEditor',
            {
                title: clusterName,
                codeData:yaml.dump(clusterData),
                callback: (backdata) => {
                    //console.log(backdata)
                    let JSONYaml = null;
                    try {
                        JSONYaml = yaml.load(backdata);
                    } catch (e) {
                        eventEmitter.emit('showToasts',{
                            type:'error',
                            msg:e.toString()
                        })
                    }
                    if(JSONYaml){
                        saveLocalClusterConfig({
                            configData:JSONYaml,
                            clusterName
                        },sbackdata=>{
                            if(sbackdata.success){
                                eventEmitter.emit('showToasts',{
                                    type:'success',
                                    msg:'save success'
                                })
                            }
                        })
                    }
                }
            }
        )
    }

    _saveAllYaml(){
        const {
            configData,
            saveConfigData
        } = this.props;
        let newJSONYaml = JSON.parse(JSON.stringify(configData));
        Object.keys(newJSONYaml.all.children).map((rCItem)=>{
            delete newJSONYaml.all.children[rCItem].edit
        })
        saveConfigData(yaml.dump(newJSONYaml),sbackData=>{
            if(sbackData.data.message === 'ok'){
                eventEmitter.emit('showToasts',{
                    type:'success',
                    msg:'save success'
                })
            }else {
                eventEmitter.emit('showToasts',{
                    type:'error',
                    msg:sbackData.data.message
                })
            }

        })
    }

    _destroyCluster(){
        const {
            clusterName,
            createJob
        } = this.props;
        eventEmitter.emit(
            'showConfirmBox',
            {
                content: 'Are you sure ?',
                confirmButton: 'Destroy',
                callback: (flag) => {
                    if (flag) {
                        createJob({
                            cmd:'?playbook=pgsql-remove&cluster='+clusterName,
                            playbook:'pgsql-remove',
                            cluster:clusterName
                        },backdata=>{
                            eventEmitter.emit(
                                'showCmdViewer',
                                {
                                    jobId:backdata.data.id,
                                    eventName: clusterName
                                }
                            )
                        })
                    }
                }
            }
        )
    }

    render() {
        const {
            clusterData,
            clusterName
        } = this.props;
        let ownHostsCount = 0;
        if (clusterData.hasOwnProperty('hosts')) {
            ownHostsCount = Object.keys(clusterData.hosts).length;
        }
        let ownPgUsersCount = 0;
        if (clusterData?.vars?.pg_users) {
            ownPgUsersCount = clusterData?.vars?.pg_users.length;
        }
        return (
            <React.Fragment>
                <Card fluid>
                    <Card.Content>
                        <Icon
                            name='server'
                            className='left floated mr-1rem'
                            size='large'
                            style={{fontSize: '3rem', color: '#2680eb'}}
                        />
                        <div className='right floated'>
                            <Button size='mini'
                                    onClick={this._editClusterVars.bind(this)}
                            ><Icon name='setting'/>Vars</Button>
                        </div>
                        <Card.Header
                            style={{fontSize: '1.4em'}}
                        >
                            {clusterName}
                        </Card.Header>
                        <div className='flex-row-left'>
                            <Card.Meta>
                                <Label size='mini' className='no-background'>
                                    <Icon name='hdd'/> {ownHostsCount}
                                </Label>
                                <Label size='mini' className='no-background'>
                                    <Icon name='users'/> {ownPgUsersCount}
                                </Label>
                            </Card.Meta>
                        </div>

                        <Divider/>

                        <div className='flex-row-between mt-1rem'>
                            <Header as='h4' style={{margin:'0'}}>
                                Hosts
                            </Header>
                            <Button size='mini'
                                    onClick={this._addNewClusterHost.bind(this)}
                            ><Icon name='plus'/>New Host</Button>
                        </div>
                        <Divider/>

                        <Card.Content>
                            {this._renderHosts()}
                        </Card.Content>


                        <div className='flex-row-between mt-1rem'>
                            <Header as='h4' style={{margin:'0'}}>
                                Pg Users
                            </Header>
                            <Button size='mini'
                                    onClick={this._manageClusterPgUser.bind(this)}
                            ><Icon name='plus'/>New Pg User</Button>
                        </div>
                        <Divider/>

                        <Card.Content>
                            {this._renderPgUsers()}
                        </Card.Content>

                        <div className='flex-row-between mt-1rem'>
                            <Header as='h4' style={{margin:'0'}}>
                                Pg Databases
                            </Header>
                            <Button size='mini'
                                    onClick={this._addNewClusterPgDatabase.bind(this)}
                            ><Icon name='plus'/>New Pg Database</Button>
                        </div>
                        <Divider/>

                        <Card.Content>
                            {this._renderPgDatabases()}
                        </Card.Content>


                    </Card.Content>
                    <Card.Content extra>
                        <div className='flex-row-right'>
                            {clusterData.edit?(
                                <Button
                                    color={'orange'}
                                    onClick={this._saveAllYaml.bind(this)}
                                >
                                    <Icon name='save'/>
                                    Save
                                </Button>
                            ):null}

                            <Popup
                                content='Init cluster'
                                trigger={
                                    <Button
                                        icon={'play'}
                                        onClick={this._runScript.bind(this)}
                                    >
                                    </Button>
                                }
                            />
                            <Popup
                                content='Destroy cluster'
                                trigger={
                                    <Button
                                        icon={'bomb'}
                                        onClick={this._destroyCluster.bind(this)}
                                    >
                                    </Button>
                                }
                            />
                            <Popup
                                content='Edit cluster Yaml'
                                trigger={
                                    <Button
                                        icon={'code'}
                                        onClick={this._editClusterYaml.bind(this)}
                                    >
                                    </Button>
                                }
                            />
                            <Popup
                                content='Delete cluster'
                                trigger={
                                    <Button
                                        icon={'trash'}
                                        onClick={this._removeCluster.bind(this)}
                                    >
                                    </Button>
                                }
                            />
                        </div>
                    </Card.Content>
                </Card>
            </React.Fragment>
        )
    }
}

const mapStateToProps = (state, props) => ({
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
    removeCluster: (formParams, callBack) => dispatch(actions.removeCluster(formParams, callBack)),
    addNewClusterHost: (formParams, callBack) => dispatch(actions.addNewClusterHost(formParams, callBack)),
    saveLocalClusterVarsConfig: (formParams, callBack) => dispatch(actions.saveLocalClusterVarsConfig(formParams, callBack)),
    saveLocalClusterConfig: (formParams, callBack) => dispatch(actions.saveLocalClusterConfig(formParams, callBack)),
    saveConfigData: (formParams, callBack) => dispatch(actions.saveConfigData(formParams, callBack)),
    createJob: (formParams, callBack) => dispatch(actions.createJob(formParams, callBack)),
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ClusterItem));
