import React from 'react'
import {connect} from 'react-redux';
import {Button, Form, Input, Select} from "semantic-ui-react";
import './index.css'

import eventEmitter from "../../commons/eventEmitter";
import Config from "../../config";

class PgsqlProvision extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            clusterName:'',
            cmd:''
        }
    }

    runScript() {
        const {
            clusterName,
            cmd
        } = this.state;
        if(clusterName.length === 0 ){
            return false;
        }
        eventEmitter.emit(
            'showConfirmBox',
            {
                content:'Are you sure run this script ?',
                confirmButton:'Run',
                callback:(flag)=>{
                    if(flag){
                        eventEmitter.emit(
                            'showCmdViewer',
                            {
                                sseUrl:Config.officialServerUrl+'/api/v1/pgsql -l '+clusterName+' '+cmd,
                                eventName:clusterName
                            }
                        )
                    }
                }
            }
        )
    }

    render() {
        const {
            configData
        } = this.props;

        const options = Object.keys(configData.all.children).map(cItem => {
            return {
                key: cItem,
                text: cItem,
                value: cItem
            }
        })
        return (
            <div className='playbook-item'>
                <div className='playbook-item-intro'>
                    <h1>Pgsql Provision</h1>
                    <div className="lead">How to define and create postgres clusters?</div>
                </div>

                <Form className='playbook-item-run-script'>
                    <Form.Field>
                        <label>Run Script</label>
                        <Form.Group inline>
                            <code className='code' style={{width: '18rem'}}>./pgsql.yml
                                -l </code>
                            <Select className='code' style={{width: '14rem'}} compact options={options}
                                    onChange={(e,{name,value})=>{
                                        this.setState({
                                            clusterName:value
                                        })
                                    }}
                            />
                            <Input fluid
                                   className='code'
                                   placeholder='--tags='
                                   onChange={(e,{name,value})=>{
                                       this.setState({
                                           cmd:value
                                       })
                                   }}
                            />
                            <Button primary type='submit'
                                onClick={this.runScript.bind(this)}
                            >Run</Button>
                        </Form.Group>
                    </Form.Field>
                </Form>


            </div>

        );
    }
}

const mapStateToProps = (state, props) => ({
    configData: state.configData
});
const mapDispatchToProps = dispatch => ({});

export default connect(mapStateToProps, mapDispatchToProps)(PgsqlProvision);
