import React from 'react'
import {Route} from 'react-router-dom'
import {connect} from 'react-redux'
import LoadingCover from "./components/loadingCover";
import actions from "./actions";

class AuthorizedRoute extends React.Component {

    componentDidMount() {
    }

    _getConfigData(){
        const {
            getConfigData,
        } = this.props;
        getConfigData({},(backData)=>{
            //
        })
    }

    render() {
        const {
            component: Component,
            //account,
            configData,
            ...rest
        } = this.props;

        if(!configData.hasOwnProperty('all')){
            this._getConfigData();
            return <LoadingCover visible={true} loadingText={'initializing...'}/>
        }

        return (
            <Route {...rest} render={props => {
                return <Component {...props} />
            }} />
        )
    }
}


const mapStateToProps = (state, props) => ({
    //account: state.account,
    configData:state.configData
});
const mapDispatchToProps = dispatch => ({
    getConfigData: (formParams, callBack) => dispatch(actions.getConfigData(formParams, callBack)),
});

export default connect(mapStateToProps, mapDispatchToProps)(AuthorizedRoute);
